<template>
  <div id="app">
    <el-container style="height: 100%;">
      <el-header :style="topBg">React管理平台</el-header>
      <el-container>
        <el-aside width="210px" :style="leftBg">
          <el-row class="tac">
            <el-col :span="24">
              <el-menu
                default-active="2"
                class="el-menu-vertical-demo"
                @open="handleOpen"
                @close="handleClose"
              >
                <template v-for="route in this.$router.options.routes">
                  <el-submenu
                    :key="route.path"
                    :index="route.path"
                    v-if="route.children && route.children.length"
                  >
                    <template slot="title">
                      <i :class="route.meta.icon"></i>
                      <span>{{route.meta.title}}</span>
                    </template>
                    <el-menu-item-group>
                      <router-link :to="todo.path" :key="todo.path" v-for="todo in route.children">
                        <el-menu-item
                          :index="todo.path"
                        >{{todo.meta.title}}</el-menu-item>
                      </router-link>
                    </el-menu-item-group>
                  </el-submenu>
 
                  <router-link
                    :to="route.path"
                    :key="route.path"
                    v-else-if="!route.children && route.path != '/'"
                  >
                    <el-menu-item :index="route.path">
                      <i :class="route.meta.icon"></i>
                      <span>{{route.meta.title}}</span>
                    </el-menu-item>
                  </router-link>
                </template>
              </el-menu>
            </el-col>
          </el-row>
        </el-aside>
        <el-main>
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>
 
<script>
export default {
  name: "App",
  components: {},
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    }
  },
  data(){
    return {
      leftBg: {
        background: "#235d8b url(" + require("./assets/left-bg.png") + ") no-repeat scroll 0 bottom",
      },
      topBg: {
        background: "#235d8b url(" + require("./assets/top-bg.png") + ") no-repeat scroll right 0",
        height: '80px',
        fontSize: "32px",
        fontWeight: 'bold',
        color: '#ffffff',
        textShadow: '0 0 5px #000000'
      }
    }
  },
  mounted(){

  }
};
</script>
 
<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  height: 100%;
}
 
.el-header,
.el-footer {
  background-color: #b3c0d1;
  color: #333;
  line-height: 80px;
}
 
.el-aside {
  background-color: #d3dce6;
  color: #ffffff;
}

.el-menu{
  background: none;
}

.el-submenu__title{
  color:#ffffff;
}
.el-submenu__title:hover{
  color:#000000;
}
.el-submenu__title i{
  color:#ffffff;
}
.el-menu-item {
  color: #ffffff;
}
.el-menu-item:hover{
  color: #000000;
}
.el-menu-item i{
  color: #ffffff;
}
.el-aside a{
  text-decoration: none;
}
 
.el-main {
  background-color: #e9eef3;
  color: #333;
}
 
body > .el-container {
  margin-bottom: 40px;
}
 
.el-container:nth-child(5) .el-aside,
.el-container:nth-child(6) .el-aside {
  line-height: 260px;
}
 
.el-container:nth-child(7) .el-aside {
  line-height: 320px;
}
</style>


